@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap";
.js-bundle-test {
  background-color: #fff8dc;
  font-family: IBM Plex Mono, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}

.js-bundle-test .content {
  width: 50%;
  width: 50vw;
  min-width: 480px;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  margin: 16px auto;
  padding: 16px 0 24px;
  display: grid;
}

.js-bundle-test .content img {
  width: 72px;
  margin-right: 8px;
  transition: all .5s cubic-bezier(.175, .885, .32, 1.275);
}

.js-bundle-test .content img:hover {
  transition: all .5s cubic-bezier(.175, .885, .32, 1.275);
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
}

.js-bundle-test .content .label {
  color: gray;
  text-align: right;
  font-size: 1em;
}

.js-bundle-test .content .data {
  color: #000;
  font-size: 1em;
  font-weight: bolder;
}

